import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';

import ReactAudioPlayer from 'react-audio-player';

const podcastItems = [
{sound: "https://dl.prokerala.com/downloads/ringtones/files/mp3/emergency-3087.mp3", label: "POD 1", date:"12-Sept-2021"},
{sound: "https://dl.prokerala.com/downloads/ringtones/files/mp3/horn-23360.mp3", label: "POD2", date:"13-Sept-2021"}
]


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 800,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: "100%",
    maxHeight: "100%",
  },
}));

export default function ComplexGrid({podcastItems}) {
	const classes = useStyles();
	return podcastItems.map((podcastItem, index) => {
	  return (
		<div className={classes.root} style={{ padding: 20 }}>
		  <Paper className={classes.paper}>
			<Grid container xs={12} spacing={2}>
			  
			  <Grid item>
				<ButtonBase className={classes.image}>
					<img className={classes.img} src={podcastItem.thumbnail}/>
				</ButtonBase>
			  </Grid>
			  <Grid item sm container>
				<Grid item xs container direction="column" spacing={2}>
				  <Grid item xs>
					<Typography gutterBottom variant="subtitle1">
					{podcastItem.label}
					</Typography>
					<Typography variant="body2" gutterBottom>
					  {podcastItem.line1}
					</Typography>
					<Typography variant="body2" color="textSecondary">
					 {podcastItem.line2}
					</Typography>
				  </Grid>
				</Grid>
				<Grid item>
				  Date: <Typography variant="subtitle1">{podcastItem.date}</Typography>
				</Grid>
				<Grid item xs={12}>
					<ReactAudioPlayer
						  src={podcastItem.sound}
						  controls
						  controlsList={"nodownload"}
						/>
			  </Grid>
			  </Grid>
			</Grid>
		  </Paper>
		</div>
	  )
	})
}
